import React from 'react'
import Element from 'react-scroll/modules/components/Element'
import './works.css'
import '../../../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import emailjs from 'emailjs-com'

const Works = () => {
    function send(e) {
     e.preventDefault();
 
     emailjs.sendForm(
       'service_asrf5r6',
       'template_4h39ifr',
        e.target,
       'user_7dsS6FK8hPcdBOPUro9LT'
       ).then(res =>{
           console.log(res);
           window.alert('Message Sent Successfully.');
       }).catch(err => console.log(err));
    }

  return (
    <Element className='workscontainer p-4' id='works'>
            <center>
                <strong className='mt-4 highlights'>Works</strong>
            </center>
            <hr/>
            <center>
              <h5>If you Need a Website (or) Any Web Related Works</h5>
            </center>
            <center><span>Get in Touch here</span></center>
            <center>
            <form className='col-md-6 mt-4' onSubmit={send} autoComplete='off'>
                <label>Name</label>
                <input className='form-control' name='Name'/><br/>
                <label>Contact Number</label>
                <input type='number' className='form-control' name='mobile'/><br/>
                <label>Any Message</label>
                <textarea className='form-control' name='message' /><br/>
                <center><button className='btn btn-success'>Send</button></center>
            </form>
            </center>
    </Element>
  )
}

export default Works
